/* App */
.jp-ElkApp {
  display: flex;
  flex-direction: column;
  flex: 1;
}

/* Toolbar Styling */
.jp-ElkApp .jp-ElkToolbar {
  width: 100%;
  visibility: hidden;
  position: absolute;
  opacity: 0;
  transition: all var(--jp-elk-transition);
  transform: translateY(calc(0px - var(--jp-widgets-inline-height)));
}

.jp-ElkApp:hover .jp-ElkToolbar {
  visibility: visible;
  opacity: 0.25;
  transform: translateY(0);
}

.jp-ElkApp:hover .jp-ElkToolbar:hover {
  opacity: 1;
}

.jp-ElkToolbar .close-btn {
  display: block;
  margin-left: auto;
  width: var(--jp-widgets-inline-height);
  padding: 0;
  background: inherit;
  border: inherit;
  outline: inherit;
}

.jp-ElkToolbar .close-btn:hover {
  box-shadow: inherit;
  color: var(--jp-warn-color0);
}

.jp-ElkSizer {
  visibility: hidden;
  z-index: -9999;
  pointer-events: none;
}
